import Vue from 'vue'
import Vuex from 'vuex'
import Wilddog from 'wilddog'
let ref = new Wilddog('https://yawenina.wilddogio.com/')

Vue.use(Vuex)

ref.child('blog').on('value', function (snapshot) {
  state.posts = snapshot.val()
})

const state = {
  posts: null,
  pages: 1
}

const mutations = {

}

export default new Vuex.Store({
  state,
  mutations
})



