<template>
  <div class="ui active inverted dimmer article" v-if="!this.article">
    <div class="ui text loader">Loading</div>
  </div>
  <div class="article" v-else>
    <h1 class="ui header">{{article.title}}</h1>
    <p><i class="icon calendar"></i>{{article.date}}</p>
    <div class="content markdown-body">{{{article.content}}}</div>
    <button v-link="{path:'/posts'}" class="ui tiny basic green icon button back"><i class="angle double left icon"></i>返回首页</button>
  </div>
  <div class="social">
    <p>分享至:</p>
    <div class="social-share"></div>
  </div>
  <div class="disqus-comments">
    <div id="disqus_thread"></div>
  </div>
</template>

<script>
  (function () { // DON'T EDIT BELOW THIS LINE
    var d = document
    var s = d.createElement('script')
    s.src = '//yawen.disqus.com/embed.js'
    s.setAttribute('data-timestamp', +new Date());
    (d.head || d.body).appendChild(s)
  })()
const marked = require('marked')
  marked.setOptions({
    highlight: function (code) {
      return require('highlight.js').highlightAuto(code).value
    }
  })
  export default {
    vuex: {
      getters: {
        posts: state => state.posts
      }
    },
    computed: {
      article: function () {
        for (let key in this.posts) {
          if (key === this.$route.query.id) {
            let result = this.posts[key]
            result.content = marked(this.posts[key].content)
            return result
          }
        }
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.article{
  padding: 50px 50px 20px;
  font-size: 1rem;
}
.social,.disqus-comments{
  padding: 10px 50px 20px;
}
.social p{
  font-weight:bold;
}
.article > h1.ui.header{
  font-size: 2rem;
  margin-bottom: 30px;
}
.article .markdown-body h1 {
  font-size: 1.5rem;
}
.article .markdown-body p{
  margin:30px auto;
}
.back{
  text-align: right;
  margin:20px 0px 10px;
}
</style>



