<template>
  <div class="ui active inverted dimmer" v-if="this.posts.length == 0">
    <div class="ui text loader">Loading</div>
  </div>
  <div class="posts">
    <div class="tags">
      <a class="ui orange tag label" @click="setTag('all')">所有文章</a>
      <a class="ui yellow tag label" @click="setTag('前端')">前端</a>
      <a class="ui green tag label" @click="setTag('书影音')">书影音</a>
      <a class="ui teal tag label" @click="setTag('生活')">生活</a>
    </div>
    <div class="ui middle aligned divided list">
      <div class="item post" v-for="post in filteredPosts | limitBy count pageIndex*count">
        <h1><a v-link="{path:'/article',query:{id:post.key}}">{{post.title}}</a></h1>
        <div class="content markdown-body" v-html="post.description | marked"></div>
        <p><a class="ui tag label"
              :class="{'yellow':post.tag == '前端','green':post.tag == '生活','teal':post.tag == '书影音'}">
          {{post.tag}}</a><a>{{post.date}}</a></p>
      </div>
    </div>
    <div class="ui right floated pagination menu">
      <a class="item" v-for="n in Math.ceil(filteredPosts.length / count)" @click="pageIndex = n" :class="{active:pageIndex == n}">{{n+1}}</a>
    </div>
  </div>

</template>

<script>
  const marked = require('marked')
  function objToArr (obj) {
    let arr = []
    let i = 0
    for (let key in obj) {
      arr[i] = obj[key]
      arr[i]['key'] = key
      i++
    }
    return arr
  }

  export default {
    data () {
      return {
        count: 5,
        pageIndex: 0,
        tag: 'all'
      }
    },
    vuex: {
      getters: {
        posts: state => objToArr(state.posts).reverse()
      }
    },
    computed: {
      filteredPosts () {
        if (this.tag === 'all') {
          return this.posts
        } else {
          return this.posts.filter(item => item.tag === this.tag)
        }
      }
    },
    methods: {
      setTag (tag) {
        this.tag = tag
        this.pageIndex = 0
      }
    },
    filters: {
      marked: marked
    }
  }

</script>

  <style scoped>
   .posts{
     padding:30px;
   }
   .ui.list > .item{
     padding:10px 0px ;
   }
  h1>a{
    color:#222
  }
  .ui.list > .item > .content{
    padding: 20px 0px;
  }
  .ui.list > .item > p {
    margin-top:10px;
    line-height: 1.5em;
  }
  .ui.list > .item > p > a{
    margin-right:20px;
  }
  .ui.tag.label{
    margin-top:10px;
    margin-right:20px;
  }
  .tags{
    margin-bottom: 0px;
  }
  .posts  .item.post{
    padding:20px 0px;
  }
  .markdown-body h1{
    font-size: 1em;
  }

  @media (min-width:992px) {
    .posts{
      padding:20px 70px 30px;;
    }
    .ui.list > .item:first-child{
      padding-top:20px;
    }
    .ui.dividied.list > .item{
      padding:20px 0px ;
    }
    .ui.list >.item h1{
      font-size: 1.5rem;
      font-weight: 600;
      margin:10px auto;
    }
    .ui.list > .item > .content{
      line-height: 2em;
      padding: 0px;
    }
    .ui.list > .item >p {
      font-size: 1.2em;
    }
    a.ui.tag.label{
      font-size:12px;
    }
    .tags{
      margin-bottom: 0px;
    }
    .ui.list >.item  .markdown-body h1{
      font-size: 1.2rem;
    }
    .posts .item.post{
      padding:30px 0px;
    }

    .posts .markdown-body p {
      font-size: 14px;
    }
  }
</style>



